import React from 'react'
import { Link } from "gatsby"
import logoMenu from '../images/uxpages_logo_darkMode.svg';
class Header extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hasScrolled: false
    }
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll)
}
handleScroll = (event) => {
  const scrollTop = window.pageYOffset

  if (scrollTop > 50) {
    this.setState({ hasScrolled: true })
  } else {
    this.setState({ hasScrolled: false })
  }
}
  render() {
    return (

      <div className={this.state.hasScrolled ? 'Header HeaderScrolled' : 'Header'}>




        <div className="availability">
          <h6>I am currently not available but feel free to get in touch !
          <Link className="fat_underline" to="#contactForm" onClick="track('Click-contact-project-topbar-index','Click-contact-project-topbar-index')">  contact me</Link></h6>
        </div>

        <nav className="sidebar-nav">
          <Link className="menu__item" to="/"><img src={logoMenu} alt="uxpages menu" /></Link>
                <Link className="menu__item" to="/case_studies/"><span className="menu__item-name">case studies</span></Link>
                <a className="menu__item" href="/#skills"><span className="menu__item-name">capabilities</span></a>
                <a className="menu__item" href="/#side_project"><span className="menu__item-name">side project</span></a>
                <Link className="menu__item" href="#contactForm"><span className="menu__item-name">contact</span></Link>
        </nav>




      </div>

    )
  }
}




export default Header
